<template>
  <li class="song-item">
    <div class="rank">
      <template v-if="index < 9"> 0{{ index + 1 }} </template>
      <template v-else>
        {{ index + 1 }}
      </template>
    </div>
    <div class="right">
      <div class="info">
        <div class="name">{{ song.name }}</div>
        <div class="author">
          <span
            v-for="(artist, index) in song.ar
              ? song.ar
              : song.artists"
            :key="index"
          >
            <template v-if="index"> / </template>{{ artist.name }}
          </span>
        </div>
      </div>
      <div class="icon" @click="togglePlay">
        <template v-if="paused && paused == true">
          <van-icon name="play-circle-o" />
        </template>
        <template v-else-if="currentmusic && currentmusic.id === song.id">
          <van-icon name="pause-circle-o" />
        </template>
        <template v-else>
          <van-icon name="play-circle-o" />
        </template>
      </div>
    </div>
  </li>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "SongItem",
  props: ["song", "index"],
  computed: {
    ...mapState(["currentmusic", "paused", "playlist"]),
  },
  methods: {
    togglePlay() {
      let song = this.song;
      let index = this.index;
      if (!song.song) {
        song.song = {};
        var artists = song.ar ? song.ar : song.artists;
        song.song.artists = artists;
      }

      this.$store.commit("updateCurrentmusic", song);
      this.$store.commit("updateCurrentindex", index);

      this.$emit("list-update-playlist");
    },
  },
};
</script>

<style lang="less" scoped>
.song-item {
  display: flex;
  align-items: center;

  .rank {
    font-size: 20px;
    margin: 0 10px;
    flex-basis: 8%;
    // &.active {
    //   color: #e13436;
    // }
  }
  .right {
    display: flex;
    align-items: center;
    flex: 1;
    border-bottom: 1px solid #ddd;
    padding: 6px 0;
    .info {
      flex: 1;
      line-height: 25px;
      .name {
        word-wrap: break-word;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 1; /*行数*/
        -webkit-box-orient: vertical;
      }
      .author {
        font-size: 14px;
        color: #999;

        word-wrap: break-word;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 1; /*行数*/
        -webkit-box-orient: vertical;
      }
    }
    .icon {
      margin-right: 20px;
      /deep/ .van-icon {
        font-size: 28px;
      }
    }
  }
}
</style>