<template>
  <div class="artists">
    <div class="pic" :style="{ backgroundImage: `url(${image})` }"></div>
    <ul>
      <SongItem
        v-for="(song, index) in artistSongs"
        :key="index"
        :song="song"
        :index="index"
        @list-update-playlist="updatePl"
      ></SongItem>
    </ul>
  </div>
</template>

<script>
import SongItem from "@/components/SongItem.vue";

export default {
  name: "Artist",
  props: ["image"],
  components: {
    SongItem,
  },
  data() {
    return {
      artistSongs: [],
      desc: "",
    };
  },
  activated() {
    this.$toast.loading({
      message: "加载中...",
      forbidClick: true,
      duration: 0,
    });

    // this.axios
    //   .get(`/artist/desc?id=${this.$route.query.id}`)
    //   .then((response) => {
    //     console.log(response.data);
    //     this.desc = response.data.briefDesc;
    //   })
    //   .catch(() => {});

    this.axios
      .get(`/artist/top/song?id=${this.$route.query.id}`)
      .then((response) => {
        // console.log(response.data.songs);
        this.artistSongs = response.data.songs;

        this.$toast.clear();
      })
      .catch(() => {
        this.$toast.clear();
      });
  },
  methods: {
    updatePl() {
      this.artistSongs = this.artistSongs.map((element) => {
        if (!element.song) {
          element.song = {};
          element.song.artists = element.ar;
        }
        return element;
      });

      this.$store.commit("updatePlaylist", this.artistSongs);
    },
  },
};
</script>

<style lang="less" scoped>
.artists {
  width: 100vw;
  height: calc(100vh - 96px);
  margin-top: 46px;
  background-color: #fff;
  position: fixed;
  z-index: 1000;
  overflow-x: scroll;
  .pic {
    width: 100vw;
    height: 60vw;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center top;
  }
}
</style>